import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, Validators } from "@angular/forms";
import { Router } from '@angular/router';
import { AlertController, NavController, Platform } from '@ionic/angular';
import { Storage } from '@ionic/storage';
import { IonLoaderServiceService } from 'src/app/services/ion-loader-service.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.page.html',
  styleUrls: ['./login.page.scss'],
})
export class LoginPage implements OnInit {
  myForm: FormGroup;
  submitted = false;
  passShow = false;
  unsubscribeBackEvent: any;

  constructor(
    private platform: Platform,
    public formBuilder: FormBuilder, 
    public alertCtrl: AlertController, 
    private router: Router,
    private nav: NavController,
    public storage: Storage,
    public loading : IonLoaderServiceService) { }

  ngOnInit() {
    this.myForm = this.formBuilder.group({
      email: ['', [Validators.required, Validators.pattern(/^[_a-z0-9-]+(\.[_a-z0-9-]+)*@[a-z0-9-]+(\.[a-z0-9-]+)*(\.[a-z]{2,3})$/)]],
      password: ['', [Validators.required, Validators.pattern(/[0-9]{6}/)]]
    })
  }

  showPass(){
    this.passShow = !this.passShow;
  }

  get errorCtr() {
    return this.myForm.controls;
  }

  onSubmit() {
    
    this.submitted = true;
    if (!this.myForm.valid) {
      this.loading.stopLoader()
      console.log('All fields are required.')
      return false;
    } else {
      this.loading.showLoader()
      console.log(this.myForm.value)
      this.storage.set("role",this.myForm.controls.password.value == '123456' ? "admin" : "staff")
      this.loading.stopLoader()
      this.showAlert("Successfully", "Login is successful")
    }
  }

  showAlert(subheader,message){
    this.alertCtrl.create({
      subHeader: subheader,
      message: message,
      buttons: [{
        text: 'OK',
        handler: () => {
          this.nav.navigateRoot(['main/tabs/post-list'])
        }
      },]
    }).then(res => {
      res.present();
    });
  }

  ionViewWillLeave(){
    this.myForm.reset()
    this.submitted = false
    this.unsubscribeBackEvent.unsubscribe();
  }

  ionViewDidEnter() {
    this.initializeBackButtonCustomHandler();
  }

  initializeBackButtonCustomHandler(): void {

    this.unsubscribeBackEvent = this.platform.backButton.subscribeWithPriority(10, () => {
      if (this.router.url === '/login') {
        if (window.confirm('Do you want to exit the app?')) {
          navigator['app'].exitApp();
        }
      } else {
        console.log('login'+this.router.url);
      }
    });
  }

}